
$(".m-select").niceSelect()

$(document).ready(function() {
    $('input[type="tel"]').mask('+7 999 999-9999', { autoclear: false }, { placeholder: '+7             ' });
});

$(".call__btn").click(function () {
    $(".call").toggleClass("_active")
})


$(document).mouseup(function(e) {
    var div = $('.call');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});

$(".spoiler-card__show").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".spoiler-card__hidden").slideToggle()
})

$(".rating-input__item").click(function() {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating-input").attr("data-total-rating", ratingItem)
    $(this).siblings(".rating-input__field").val(ratingItem)
})

$("[data-link-href]").click(function () {
    let linkHref = $(this).attr("data-link-href")
    window.location.href = linkHref;
})
