$(".filter-select__show").click(function () {
    $(this).parents(".filter-select").toggleClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('.filter-select__dropdown');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).parents(".filter-select").hasClass("_active")){
            $(div).parents(".filter-select").removeClass("_active")
        }
    }
});

$(".filter-select__apply").click(function () {
    $(this).parents(".filter-select").removeClass("_active")
})

$(".filter-select__refresh").click(function () {
    $(this).parents(".filter-select").removeClass("_active")
    $(this).parents(".filter-select").find(".filter-select__check input").each(function (index, el){
        $(el).prop("checked",false)
    });
})


$(window).resize(function () {
    // setDropdownPosition()
})

// setDropdownPosition()

function setDropdownPosition(){
    if(window.innerWidth>577){
        return
    }
    $(".filter-select__dropdown").each(function (index, el){
        let position = $(el).offset().left;
        $(el).css("transform",`translateX(${-(position)}px)`)
    });
    $(".filter-select .list").each(function (index, el){
        console.log($(el))
        let position = $(el).offset().left;
        $(el).css("transform",`translateX(${-(position)}px)`)
    });

}




